'use strict';

var doc = document;

// Выравнивание бллоков по высоте

var alignItems = doc.getElementsByClassName('height-align');

getAlignItems(alignItems);

function clearStyle() {
	for (var i = 0; i < alignItems.length; i++) {
		alignItems[i].removeAttribute('style');
	}
	getAlignItems(alignItems);
}

function getAlignItems(items) {
	var maxItemlHeight = 0;

	for (var i = 0; i < items.length; i++) {
		if (maxItemlHeight < items[i].clientHeight) {
			maxItemlHeight = items[i].clientHeight;
		}
	}
	maxItemlHeight += 'px';

	for (var _i = 0; _i < items.length; _i++) {
		items[_i].style.height = maxItemlHeight;
	}
	window.addEventListener('resize', clearStyle);
}

// Запуска анимации самолётиков

var planes = doc.getElementsByClassName('title-btn');

doc.addEventListener('scroll', toStart);

function toStart() {
	var currentScroll = pageYOffset + doc.documentElement.clientHeight / 2;

	for (var i = 0; i < planes.length; i++) {
		var coords = planes[i].getBoundingClientRect();
		var positionY = coords.top + pageYOffset;

		if (currentScroll >= positionY) {
			planes[i].firstElementChild.classList.add('plane-fly');
		}
	}
}